import React, { useState, useEffect, useContext } from 'react';
import {
  Tooltip,
  IconButton,
  Box,
  styled,
  InputBase,
  useTheme,
  Menu,
  MenuItem,
  Divider,
  Typography,
  CircularProgress
} from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import EmojiPicker from "emoji-picker-react";
import swal from 'sweetalert2';
import i18next from 'i18next';
import ScreenCapture from 'src/ScreenCapture';
import {
  setMessages,
  sendMessage,
  stopGeneration,
  generateImage,
  generateImagenImage,
  stopImageGeneration
} from 'src/actions/chatAction';
import { selectModel } from 'src/actions/settingAction';
import { SidebarContext } from 'src/contexts/SidebarContext';
import { useAuth } from 'src/contexts/AuthContext';
import { useTranslation } from 'react-i18next';
import {
  SET_MESSAGES
} from 'src/actions/types';
import InsertDriveFileOutlinedIcon from '@mui/icons-material/InsertDriveFileOutlined';
import AddPhotoAlternateOutlinedIcon from '@mui/icons-material/AddPhotoAlternateOutlined';
import CancelIcon from '@mui/icons-material/Cancel';
import StopCircleIcon from '@mui/icons-material/StopCircle';
import ImageIcon from '@mui/icons-material/Image';

const MessageInputWrapper = styled(InputBase)(({ theme }) => ({
  fontSize: theme.typography.pxToRem(18),
  padding: theme.spacing(1),
  width: '100%',
  maxHeight: theme.typography.pxToRem(162),
  lineHeight: 1.6,
  '& .MuiInputBase-input': {
    lineHeight: 1.6,
    '&::placeholder': {
      lineHeight: 1.6
    }
  }
}));

const ImageGenerationModeBox = styled(Box)(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  background: 'rgba(16, 163, 127, 0.1)',
  borderRadius: theme.shape.borderRadius,
  padding: theme.spacing(0.5, 1),
  marginBottom: theme.spacing(1),
  border: '1px solid rgba(16, 163, 127, 0.2)'
}));

function BottomBarContent({ file: propFile }) {
  const theme = useTheme();
  const { user, getUserData } = useAuth();
  const { selectedFile } = useContext(SidebarContext);
  const dispatch = useDispatch();
  const model = useSelector((state) => state.setting.model);
  const messages = useSelector((state) => state.chat.messages);
  const selectedChatLog = useSelector((state) => state.chat.selectedChatLog);
  const activeChatId = useSelector((state) => state.chat.activeChatId);
  const isGenerating = useSelector((state) => state.chat.isGenerating);
  const imageGenerating = useSelector((state) => state.chat.imageGenerating);
  const imageError = useSelector((state) => state.chat.imageError);
  const { t } = useTranslation();
  const [file, setFile] = useState(null);
  const [filePreview, setFilePreview] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [anchorE2, setAnchorE2] = useState(null);
  const [input, setInput] = useState('');
  const [screenCapture, setScreenCapture] = useState(null);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const [isDragging, setIsDragging] = useState(false);
  const [currentSize, setCurrentSize] = useState('1024x1024');
  const [currentQuality, setCurrentQuality] = useState('standard');
  const [currentStyle, setCurrentStyle] = useState('vivid');
  const [previousMessages, setPreviousMessages] = useState([]);
  const [currentMessageIndex, setCurrentMessageIndex] = useState(-1);

  const isImageModel = model && model.isImageModel;

  useEffect(() => {
    previewFile(propFile);
  }, [propFile]);

  useEffect(() => {
    previewFile(selectedFile);
  }, [selectedFile]);

  useEffect(() => {
    document.addEventListener("paste", handlePaste);
    return () => {
      document.removeEventListener("paste", handlePaste);
    };
  }, []);

  useEffect(() => {
    if (user?.balance <= 0) {
      dispatch(selectModel({
        id: 'gpt-4o-mini',
        label: 'GPT-4o mini',
        icons: [],
        requiresPayment: false
      }));
    }
  }, [user]);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handlePaste = (event) => {
    const clipboardItems = event.clipboardData.items;
    for (const item of clipboardItems) {
      if (item.type.indexOf("image") !== -1) {
        const blob = item.getAsFile();
        const reader = new FileReader();
        reader.onload = (e) => {
          setScreenCapture(e.target.result);
        };
        reader.readAsDataURL(blob);
        break;
      }
    }
  };

  const previewFile = (propFile) => {
    if (propFile) {
      setFile(propFile);
      const fileUrl = URL.createObjectURL(propFile);
      if (propFile.type.startsWith('image/')) {
        const reader = new FileReader();
        reader.onloadend = () => {
          setScreenCapture(reader.result);
        };
        reader.readAsDataURL(propFile);
      } else if (propFile.type === "application/pdf" || propFile.type === "application/msword" ||
        propFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document") {
        setFilePreview(fileUrl);
      } else {
        swal.fire({
          title: i18next.t("Please upload a valid file(pdf, doc, docx, images)"),
          icon: "error",
          toast: true,
          timer: 3000,
          position: 'bottom-right',
          customClass: {
            popup: 'swal-popup',
            container: 'swal-container',
            title: 'swal-title',
            icon: 'swal-icon'
          },
          timerProgressBar: true,
          showConfirmButton: false
        });
      }
    }
  };

  const handleScreenCaptureMessage = () => {
    if (screenCapture) {
      const newMessage = {
        role: 'user',
        content: input || 'Screenshot',
        base64Encoded: screenCapture
      };
      dispatch({
        type: SET_MESSAGES,
        payload: {
          newMessage: newMessage
        }
      });
      setInput('');
      const formData = new FormData();
      const base64Data = screenCapture.split(',')[1];
      const blob = base64ToBlob(base64Data, 'image/png');
      const formattedMessages = [...selectedChatLog].map(({ role, content }) => ({
        role: role === 'assistant' ? 'assistant' : 'user',
        content: content
      }));
      formattedMessages.push({
        role: 'user',
        content: [
          {
            type: 'text',
            text: input || 'Screenshot'
          },
          {
            type: 'image_url',
            image_url: {
              url: screenCapture
            }
          }
        ]
      });
      formData.append('message', input || 'Screenshot');
      formData.append('model', model?.id || 'gpt-4o-mini');
      formData.append('file', blob, 'screenshot.png');
      formData.append('base64Encoded', screenCapture);
      formData.append("formattedMessages", JSON.stringify(formattedMessages));
      if (user) {
        formData.append('user_id', user.id);
        if (activeChatId != null) {
          formData.append('chat_id', activeChatId);
        }
        dispatch(sendMessage(formData, user.id, activeChatId));
      } else {
        dispatch(sendMessage(formData));
      }
      setScreenCapture(null);
    }
  };

  const base64ToBlob = (base64, mimeType) => {
    const byteCharacters = atob(base64);
    const byteArrays = [];
    for (let i = 0; i < byteCharacters.length; i += 512) {
      const slice = byteCharacters.slice(i, i + 512);
      const byteNumbers = new Array(slice.length);
      for (let j = 0; j < slice.length; j++) {
        byteNumbers[j] = slice.charCodeAt(j);
      }
      byteArrays.push(new Uint8Array(byteNumbers));
    }
    return new Blob(byteArrays, { type: mimeType });
  };

  const fireEvent = (eventName, eventData) => {
    console.log(`Event: ${eventName}`, eventData);
  };

  const handleSendMessage = () => {
    if (screenCapture) {
      handleScreenCaptureMessage();
      return;
    }
    if (model && model.isImageModel && input.trim()) {
      handleImageGeneration();
      return;
    }
    if (!input.trim()) return;
    const newMessage = {
      role: 'user',
      content: input,
      file: filePreview ? file.name : null,
      base64Encoded: screenCapture ? screenCapture : null,
      model: model?.id || 'default'
    };
    dispatch(setMessages({
      message: newMessage
    }));
    setPreviousMessages([...previousMessages, input]);
    setCurrentMessageIndex(-1);
    let formattedMessages = [...selectedChatLog].map(({ role, content }) => ({
      role: role === 'assistant' ? 'assistant' : 'user',
      content: content
    }));
    if (screenCapture) {
      formattedMessages.push({
        role: 'user',
        content: [
          {
            type: 'text',
            text: input
          },
          {
            type: 'image_url',
            image_url: {
              url: screenCapture
            }
          }
        ]
      });
    } else {
      formattedMessages.push({
        role: 'user',
        content: input
      });
    }
    const formData = new FormData();
    if (file != null) {
      formData.append("file", file);
    }
    if (screenCapture != null) {
      formData.append("base64Encoded", screenCapture);
    }
    if (user) {
      formData.append('user_id', user.id);
      if (activeChatId != null) {
        formData.append('chat_id', activeChatId);
      }
    }
    formData.append('model', model.id);
    formData.append("message", input);
    formData.append("formattedMessages", JSON.stringify(formattedMessages));
    setInput('');
    setFile(null);
    setFilePreview(null);
    setScreenCapture(null);
    if (user) {
      try {
        const res = dispatch(sendMessage(formData, user.id, activeChatId));
        if (res) {
          getUserData();
        }
      } catch (error) {
        console.error("Failed to send message:", error);
      }
    } else {
      dispatch(sendMessage(formData));
    }
  };

  const handleImageGeneration = () => {
    const imageOptions = {
      prompt: input,
      model: model?.id,
      n: 1
    };
    if (currentSize) {
      imageOptions.size = currentSize;
    }
    if (currentQuality) {
      imageOptions.quality = currentQuality;
    }
    if (currentStyle) {
      imageOptions.style = currentStyle;
    }
    setInput('');
    if (model && model.useImagenAPI) {
      dispatch(generateImagenImage(imageOptions, user?.id, activeChatId));
    } else {
      dispatch(generateImage(imageOptions, user?.id, activeChatId));
    }
    fireEvent('image_generation_attempt', {
      modelId: model.id,
      isImageModel: true
    });
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      if (isMobile) {
        return;
      }
      if (!e.shiftKey) {
        e.preventDefault();
        handleSendMessage();
      }
    }
  };

  const handleKeyDown = (e) => {
    if (e.ctrlKey && e.key === 'ArrowUp') {
      e.preventDefault();
      if (previousMessages.length > 0) {
        const newIndex = currentMessageIndex === -1 ? previousMessages.length - 1 : currentMessageIndex - 1;
        setCurrentMessageIndex(newIndex);
        setInput(previousMessages[newIndex]);
      }
    }
  };

  const handleEmojiClick = (emojiObject) => {
    setInput((prev) => prev + emojiObject.emoji);
    setAnchorEl(null);
  };

  const handleFileChange = (event) => {
    const selectedFile = event.target.files[0];
    if (selectedFile) {
      setFile(selectedFile);
      if (selectedFile.type.startsWith('image/')) {
        const reader = new FileReader();
        reader.onloadend = () => {
          setScreenCapture(reader.result);
        };
        reader.readAsDataURL(selectedFile);
      } else if (selectedFile.type === "application/pdf" || selectedFile.type === "application/msword" ||
        selectedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document") {
        const fileUrl = URL.createObjectURL(selectedFile);
        setFilePreview(fileUrl);
      } else {
        swal.fire({
          title: i18next.t("Please upload a valid file(pdf, doc, docx, images)"),
          icon: "error",
          toast: true,
          timer: 3000,
          position: 'bottom-right',
          customClass: {
            popup: 'swal-popup',
            container: 'swal-container',
            title: 'swal-title',
            icon: 'swal-icon'
          },
          timerProgressBar: true,
          showConfirmButton: false
        });
        return;
      }
    }
    setAnchorE2(null);
  };

  const handleScreenCapture = (capturedImage) => {
    if (capturedImage !== "data:,") {
      setScreenCapture(capturedImage);
    }
  };

  const handleStopGeneration = () => {
    dispatch(stopGeneration());
  };

  const handleStopImageGeneration = () => {
    dispatch(stopImageGeneration());
  };

  const Preview = ({ type, children, onDelete }) => (
    <Box
      sx={{
        position: 'relative',
        p: type == "img" ? '3px' : '7px',
        maxWidth: type == "img" ? 50 : 250,
        height: 50,
        border: '1px solid gray',
        borderRadius: 1,
        display: 'flex',
        flex: 'none',
        alignItems: 'center',
        color: 'black',
        gap: 0.5,
        mx: 1
      }}
    >
      {children}
      <IconButton
        sx={{ color: 'black', position: 'absolute', right: -17, top: -17, fontSize: 20 }}
        onClick={onDelete}
      >
        <CancelIcon />
      </IconButton>
    </Box>
  );

  const handleDragOver = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(true);
  };

  const handleDragLeave = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);
  };

  const handleDrop = (e) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);
    const droppedFile = e.dataTransfer.files[0];
    if (droppedFile) {
      if (droppedFile.type.startsWith('image/')) {
        const reader = new FileReader();
        reader.onloadend = () => {
          setScreenCapture(reader.result);
        };
        reader.readAsDataURL(droppedFile);
      } else if (
        droppedFile.type === "application/pdf" ||
        droppedFile.type === "application/msword" ||
        droppedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
      ) {
        const fileUrl = URL.createObjectURL(droppedFile);
        setFile(droppedFile);
        setFilePreview(fileUrl);
      } else {
        swal.fire({
          title: i18next.t("Please upload a valid file(pdf, doc, docx, images)"),
          icon: "error",
          toast: true,
          timer: 3000,
          position: 'bottom-right',
          customClass: {
            popup: 'swal-popup',
            container: 'swal-container',
            title: 'swal-title',
            icon: 'swal-icon'
          },
          timerProgressBar: true,
          showConfirmButton: false
        });
      }
    }
  };

  return (
    <ScreenCapture onEndCapture={handleScreenCapture}>
      {({ onStartCapture }) => (
        <Box
          sx={{
            background: 'white',
            display: 'flex',
            flexDirection: 'column',
            px: 2,
            py: 1,
            position: 'relative'
          }}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
        >
          {isDragging && (
            <Box
              sx={{
                position: 'absolute',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                backgroundColor: 'rgba(38, 90, 158, 0.05)',
                border: '2px dashed #265A9E',
                borderRadius: 2,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                zIndex: 10
              }}
            >
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  gap: 2
                }}
              >
                <svg width="40" height="40" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M17.8033 10.4665L9.94233 18.0854C8.75873 19.2326 8.75873 21.0935 9.94233 22.2407V22.2407C11.1259 23.3878 13.046 23.3878 14.2296 22.2407L24.5925 12.1969C26.7634 10.0929 26.7634 6.68204 24.5925 4.57802V4.57802C22.4216 2.47399 18.9024 2.47399 16.7315 4.57802L6.36862 14.6218C3.21046 17.6827 3.21046 22.6434 6.36862 25.7043V25.7043C9.52678 28.7652 14.6452 28.7652 17.8033 25.7043L24.0924 19.6089" stroke="#265A9E" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                </svg>
                <Typography sx={{ color: '#265A9E', fontWeight: 500 }}>
                  {t("Drop your file here")}
                </Typography>
              </Box>
            </Box>
          )}
          <Box flexGrow={1} display="flex" flexDirection="column" width="100%">
            {isImageModel && (
              <ImageGenerationModeBox>
                <ImageIcon sx={{ color: '#10a37f', mr: 1, fontSize: 20 }} />
                <Typography variant="body2" sx={{ color: '#10a37f', fontWeight: 500 }}>
                  {model?.useImagenAPI
                    ? t('Imagen 3 Image Mode')
                    : model?.id === 'dall-e-3'
                      ? t('DALL-E 3 Image Mode')
                      : t('DALL-E 2 Image Mode')}
                </Typography>
              </ImageGenerationModeBox>
            )}
            {(filePreview || screenCapture) && (
              <Box display="flex" alignItems="center" mb={1}>
                {filePreview && (
                  <Preview type="file" onDelete={() => setFilePreview(null)}>
                    <div style={{ width: 30 }}>
                      <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M17.8033 10.4665L9.94233 18.0854C8.75873 19.2326 8.75873 21.0935 9.94233 22.2407V22.2407C11.1259 23.3878 13.046 23.3878 14.2296 22.2407L24.5925 12.1969C26.7634 10.0929 26.7634 6.68204 24.5925 4.57802V4.57802C22.4216 2.47399 18.9024 2.47399 16.7315 4.57802L6.36862 14.6218C3.21046 17.6827 3.21046 22.6434 6.36862 25.7043V25.7043C9.52678 28.7652 14.6452 28.7652 17.8033 25.7043L24.0924 19.6089" stroke="#667085" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                      </svg>
                    </div>
                    <span style={{ overflow: 'hidden', textOverflow: "ellipsis", whiteSpace: "nowrap" }}>{file.name}</span>
                  </Preview>
                )}
                {screenCapture && (
                  <Preview type="img" onDelete={() => setScreenCapture(null)}>
                    <Box component="img" src={screenCapture} alt="Screen Capture" sx={{ borderRadius: 1, width: '100%', height: '100%' }} />
                  </Preview>
                )}
              </Box>
            )}
            <MessageInputWrapper
              autoFocus
              placeholder={
                isGenerating
                  ? t('Generating Response')
                  : imageGenerating
                    ? t('Generating Image')
                    : isImageModel
                      ? model?.useImagenAPI
                        ? t('Describe Image for Imagen to Generate')
                        : t('Describe Image To Generate')
                      : t('Write a message. Use Ctrl + Up to view the previous message.')
              }
              multiline
              disabled={isGenerating || imageGenerating}
              value={input}
              onChange={(e) => setInput(e.target.value)}
              onKeyDown={handleKeyDown}
              maxRows={9}
              sx={{
                fontSize: 15,
                color: '#667085',
                '& .MuiInputBase-input': {
                  maxHeight: '162px',
                  overflowY: 'auto'
                }
              }}
            />
          </Box>
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              width: "100%",
              alignItems: "center",
              mt: 1
            }}
          >
            <Box sx={{ display: "flex", alignItems: "center" }}>
              {!isImageModel && (
                <>
                  <Tooltip arrow title={t("chooseEmoji")}>
                    <IconButton onClick={(e) => setAnchorEl(e.currentTarget)}>
                      <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <g clipPath="url(#clip0_28_1551)">
                          <path d="M15 0C6.72896 0 0 6.72896 0 15C0 23.271 6.72896 30 15 30C23.271 30 30 23.271 30 15C30 6.72896 23.271 0 15 0ZM15 28.125C7.76285 28.125 1.875 22.2371 1.875 15C1.875 7.76285 7.76285 1.875 15 1.875C22.2371 1.875 28.125 7.76285 28.125 15C28.125 22.2371 22.2371 28.125 15 28.125ZM23.0595 19.1719C22.0814 20.3634 20.8512 21.3232 19.4576 21.9822C18.0639 22.6412 16.5415 22.983 14.9999 22.983C13.4584 22.983 11.9359 22.6412 10.5423 21.9822C9.1487 21.3232 7.91852 20.3634 6.94037 19.1719C6.86173 19.0768 6.80263 18.9672 6.76645 18.8492C6.73027 18.7313 6.71772 18.6074 6.72952 18.4846C6.74133 18.3618 6.77725 18.2425 6.83524 18.1336C6.89323 18.0247 6.97213 17.9284 7.06744 17.85C7.16274 17.7717 7.27257 17.7129 7.39062 17.6771C7.50868 17.6413 7.63264 17.6292 7.7554 17.6414C7.87816 17.6536 7.99731 17.6899 8.10601 17.7482C8.21472 17.8065 8.31084 17.8858 8.38887 17.9813C9.19113 18.9588 10.2002 19.7463 11.3433 20.2869C12.4865 20.8276 13.7353 21.108 14.9999 21.108C16.2645 21.108 17.5133 20.8276 18.6565 20.2869C19.7997 19.7463 20.8087 18.9588 21.611 17.9813C21.7691 17.7901 21.9966 17.6694 22.2436 17.6457C22.4906 17.622 22.7369 17.6971 22.9286 17.8546C23.1203 18.0122 23.2417 18.2393 23.2662 18.4862C23.2907 18.7331 23.2164 18.9797 23.0595 19.1719H23.0595ZM17.1759 12.4075V11.748C17.1759 10.3672 18.4639 9.24387 20.047 9.24387C21.6301 9.24387 22.9181 10.3672 22.9181 11.748V12.4078C22.9181 12.6565 22.8194 12.8949 22.6435 13.0707C22.4677 13.2465 22.2293 13.3453 21.9806 13.3453C21.732 13.3453 21.4935 13.2465 21.3177 13.0707C21.1419 12.8949 21.0431 12.6565 21.0431 12.4078V11.748C21.0431 11.451 20.6171 11.1189 20.047 11.1189C19.477 11.1189 19.0509 11.451 19.0509 11.748V12.4078C19.0509 12.6565 18.9522 12.8949 18.7764 13.0707C18.6005 13.2465 18.3621 13.3453 18.1134 13.3453C17.8648 13.3453 17.6263 13.2465 17.4505 13.0707C17.2747 12.8949 17.1759 12.6565 17.1759 12.4078V12.4075ZM7.08158 12.4075V11.748C7.08158 10.3672 8.36965 9.24387 9.95268 9.24387C11.5357 9.24387 12.8238 10.3672 12.8238 11.748V12.4078C12.8238 12.6565 12.725 12.8949 12.5492 13.0707C12.3734 13.2465 12.1349 13.3453 11.8863 13.3453C11.6376 13.3453 11.3992 13.2465 11.2234 13.0707C11.0475 12.8949 10.9488 12.6565 10.9488 12.4078V11.748C10.9488 11.451 10.5228 11.1189 9.95268 11.1189C9.38256 11.1189 8.95658 11.451 8.95658 11.748V12.4078C8.95658 12.6565 8.85781 12.8949 8.682 13.0707C8.50618 13.2465 8.26772 13.3453 8.01908 13.3453C7.77044 13.3453 7.53198 13.2465 7.35617 13.0707C7.18035 12.8949 7.08158 12.6565 7.08158 12.4078V12.4075Z" fill="#667085" />
                        </g>
                        <defs>
                          <clipPath id="clip0_28_1551">
                            <rect width="30" height="30" fill="white" />
                          </clipPath>
                        </defs>
                      </svg>
                    </IconButton>
                  </Tooltip>
                  <Tooltip arrow title={t("sendFile")}>
                    <IconButton onClick={(e) => setAnchorE2(e.currentTarget)}>
                      <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M17.8033 10.4665L9.94233 18.0854C8.75873 19.2326 8.75873 21.0935 9.94233 22.2407V22.2407C11.1259 23.3878 13.046 23.3878 14.2296 22.2407L24.5925 12.1969C26.7634 10.0929 26.7634 6.68204 24.5925 4.57802V4.57802C22.4216 2.47399 18.9024 2.47399 16.7315 4.57802L6.36862 14.6218C3.21046 17.6827 3.21046 22.6434 6.36862 25.7043V25.7043C9.52678 28.7652 14.6452 28.7652 17.8033 25.7043L24.0924 19.6089" stroke="#667085" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                      </svg>
                    </IconButton>
                  </Tooltip>
                </>
              )}
            </Box>
            {isGenerating ? (
              <Tooltip arrow title={t("stopGeneration")}>
                <IconButton
                  onClick={handleStopGeneration}
                  sx={{
                    color: '#d32f2f',
                    '&:hover': {
                      backgroundColor: 'rgba(211, 47, 47, 0.04)'
                    }
                  }}
                >
                  <StopCircleIcon fontSize="large" />
                </IconButton>
              </Tooltip>
            ) : imageGenerating ? (
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                <Box sx={{ display: 'flex', alignItems: 'center' }}>
                  <CircularProgress size={20} sx={{ color: '#10a37f', mr: 1 }} />
                  <Typography variant="body2" color="text.secondary">
                    {t('generatingImage')}...
                  </Typography>
                </Box>
                <Tooltip arrow title={t("stopImageGeneration")}>
                  <IconButton
                    onClick={handleStopImageGeneration}
                    sx={{
                      color: '#d32f2f',
                      '&:hover': {
                        backgroundColor: 'rgba(211, 47, 47, 0.04)'
                      }
                    }}
                  >
                    <StopCircleIcon fontSize="large" />
                  </IconButton>
                </Tooltip>
              </Box>
            ) : (
              <IconButton onClick={handleSendMessage}>
                {isImageModel ? (
                  <ImageIcon sx={{ color: '#10a37f', fontSize: 24 }} />
                ) : (
                  <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M27.6263 13.2025L3.44125 1.40374C3.20884 1.29048 2.95142 1.23822 2.69324 1.25188C2.43506 1.26554 2.1846 1.34467 1.96544 1.48182C1.74627 1.61898 1.56561 1.80964 1.44045 2.03587C1.31529 2.26209 1.24975 2.51645 1.25 2.77499V2.81874C1.25012 3.02313 1.27531 3.22673 1.325 3.42499L3.645 12.705C3.6761 12.8286 3.74426 12.9398 3.84033 13.0236C3.9364 13.1073 4.0558 13.1597 4.1825 13.1737L14.3788 14.3075C14.5484 14.3279 14.7047 14.4098 14.8181 14.5376C14.9315 14.6654 14.9941 14.8304 14.9941 15.0012C14.9941 15.1721 14.9315 15.3371 14.8181 15.4649C14.7047 15.5927 14.5484 15.6746 14.3788 15.695L4.1825 16.8287C4.0558 16.8427 3.9364 16.8951 3.84033 16.9789C3.74426 17.0627 3.6761 17.1739 3.645 17.2975L1.325 26.5762C1.27531 26.7745 1.25012 26.9781 1.25 27.1825V27.2262C1.24996 27.4847 1.31565 27.7389 1.4409 27.9649C1.56615 28.191 1.74683 28.3815 1.96597 28.5185C2.1851 28.6555 2.43548 28.7345 2.69356 28.7481C2.95163 28.7617 3.20893 28.7095 3.44125 28.5962L27.625 16.7975C27.9618 16.6332 28.2457 16.3776 28.4442 16.0597C28.6428 15.7419 28.7481 15.3747 28.7481 15C28.7481 14.6253 28.6428 14.258 28.4442 13.9402C28.2457 13.6224 27.9631 13.3668 27.6263 13.2025Z" fill="#667085" />
                  </svg>
                )}
              </IconButton>
            )}
          </Box>
          <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={() => setAnchorEl(null)}>
            <EmojiPicker onEmojiClick={handleEmojiClick} />
          </Menu>
          <Menu
            anchorEl={anchorE2}
            open={Boolean(anchorE2)}
            onClose={() => setAnchorE2(null)}
            PaperProps={{
              sx: {
                padding: 0,
              },
            }}
            MenuListProps={{
              sx: {
                padding: 0,
              },
            }}
            sx={{
              '.MuiMenu-paper': {
                top: 'unset !important',
                bottom: '65px !important'
              },
            }}
          >
            <label htmlFor="messenger-upload-file">
              <MenuItem
                sx={{
                  color: 'black !important',
                  borderRadius: 0,
                  py: 1,
                  '&:hover': {
                    color: 'white !important',
                    backgroundColor: '#265A9E',
                  },
                }}
              >
                <input
                  id="messenger-upload-file"
                  type="file"
                  accept="image/jpeg, image/png, application/pdf, .doc, .docx"
                  style={{ display: 'none' }}
                  onChange={handleFileChange}
                />
                <InsertDriveFileOutlinedIcon /> {t('sendFile')}
              </MenuItem>
            </label>
            <Divider sx={{ my: '0px !important' }} />
            <MenuItem
              onClick={() => {
                onStartCapture();
                setAnchorE2(null);
              }}
              sx={{
                color: 'black !important',
                borderRadius: 0,
                py: 1,
                '&:hover': {
                  color: 'white !important',
                  backgroundColor: '#265A9E',
                },
              }}
            >
              <AddPhotoAlternateOutlinedIcon /> {t("attachScreenshort")}
            </MenuItem>
          </Menu>
        </Box>
      )}
    </ScreenCapture>
  );
}

export default BottomBarContent;
