import { useState, useRef } from 'react';

import { Helmet } from 'react-helmet-async';


import BottomBarContent from './BottomBarContent';
import SidebarContent from './SidebarContent';
import ChatContent from './ChatContent';
import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

import Scrollbar from 'src/components/Scrollbar';
import { useDevice } from 'src/contexts/deviceContext';

import {
  Box,
  styled,
  Divider,
  Drawer,
  IconButton,
  useTheme,
  Snackbar,
  Alert
} from '@mui/material';

const RootWrapper = styled(Box)(({ theme }) => ({
  display: "flex",
  height: `calc(100dvh - ${theme.header.mobileHeight})`,
  [theme.breakpoints.up("md")]: {
    height: `calc(100dvh - ${theme.header.height})`
  }
}));

const Sidebar = styled(Box)(
  ({ theme }) => `
        width: 300px;
        background: #E8E8E8; // Slightly lighter background color
        border-right: ${theme.colors.alpha.black[10]} solid 1px;
`
);

const ChatWindow = styled(Box)(
  () => `
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        flex: 1;
`
);

const ChatTopBar = styled(Box)(
  ({ theme }) => `
        background: ${theme.colors.alpha.white[100]};
        border-bottom: ${theme.colors.alpha.black[10]} solid 1px;
        padding: ${theme.spacing(2)};
        align-items: center;
`
);

const IconButtonToggle = styled(IconButton)(
  ({ theme }) => `
  width: ${theme.spacing(4)};
  height: ${theme.spacing(4)};
  background: ${theme.colors.alpha.white[100]};
`
);

const DrawerWrapperMobile = styled(Drawer)(
  () => `
    width: 340px;
    flex-shrink: 0;

  & > .MuiPaper-root {
        width: 340px;
        z-index: 3;
  }
`
);



function ApplicationsMessenger() {
  const theme = useTheme();
  const { isMobile, isComputer } = useDevice();
  const [file, setFile] = useState(null);
  const [mobileOpen, setMobileOpen] = useState(false);
  const chatScrollRef = useRef(null);

  const location = useLocation();
  
  const [toast, setToast] = useState({
    open: false,
    message: '',
    severity: 'success'
  });

  const handleCloseToast = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }
    setToast({ ...toast, open: false });
  };

  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const paymentStatus = params.get('payment');
    const sessionId = params.get('session_id');

    if (sessionId) {
      // Call your backend to verify the payment
      fetch(`/api/payment/stripe_success?session_id=${sessionId}`, {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
        },
      });
    }

    if (paymentStatus) {
      let message = '';
      let severity = 'info';

      switch (paymentStatus) {
        case 'success':
          message = 'Payment successful! Your balance has been updated.';
          severity = 'success';
         
          break;
        case 'cancelled':
          message = 'Payment was cancelled.';
          severity = 'warning';
          break;
        case 'error':
          const reason = params.get('reason');
          message = `Payment error: ${reason || 'Unknown error'}`;
          severity = 'error';
          break;
        case 'not-paid':
          message = 'Payment was not completed.';
          severity = 'error';
          break;
        case 'already-processed':
          message = 'Payment was already processed.';
          severity = 'info';
          break;
        default:
          message = 'Unknown payment status.';
          severity = 'warning';
      }
      
      setToast({
        open: true,
        message,
        severity
      });
    }
  }, [location.search]);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleChatScroll = (e) => {
    if (chatScrollRef.current && chatScrollRef.current.handleScroll) {
      chatScrollRef.current.handleScroll(e);
    }
  };


  

  return (
    <>
      <Helmet>
        <title>OneDollarAI</title>
      </Helmet>
      <RootWrapper className="Mui-FixedWrapper">
        <Snackbar
          open={toast.open}
          autoHideDuration={6000}
          onClose={handleCloseToast}
          anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        >
          <Alert 
            onClose={handleCloseToast} 
            severity={toast.severity}
            sx={{ width: '100%' }}
            elevation={6}
            variant="filled"
          >
            {toast.message}
          </Alert>
        </Snackbar>
        <DrawerWrapperMobile
          sx={{
            display: { lg: 'block', xs: 'inline-block' }
          }}
          variant="temporary"
          anchor={theme.direction === 'rtl' ? 'right' : 'left'}
          open={mobileOpen}
          onClose={handleDrawerToggle}
        >
        </DrawerWrapperMobile>
          { !isMobile && (
            <Sidebar>
              <Scrollbar sx={{ backgroundColor: '#FFFFFF' }}>
                <SidebarContent />
              </Scrollbar>
            </Sidebar>
          )}
        <ChatWindow 
          sx={{
            pt:{
              // xs: isComputer ? `41px` : `90px`,
              md : '0px'
          }}}
        >
          <Box flex={1}
            sx={{
                position:'relative',
                pt:{
                    md:'0px !important'
                }
            }}
           
            >
          
            <Scrollbar onScroll={handleChatScroll}>
              <ChatContent ref={chatScrollRef} />
            </Scrollbar>
          </Box>
          <Divider />
          <BottomBarContent file={file} />
        </ChatWindow>
      </RootWrapper>
    </>
  );
}

export default ApplicationsMessenger;