const getModelDetails = (modelName) => {
  let icon = <SmsIcon style={{ fontSize: '16px' }} />;
  let color = '#666';
  let bgColor = '#f4f5f7';
  let displayName = modelName || 'Unknown Model';

  // DALL-E models
  if (modelName?.includes('DALL-E')) {
    icon = <ImageIcon style={{ fontSize: '16px' }} />;
    color = '#ab68ff'; // purple for DALL-E
    bgColor = '#fcf7ff';
    displayName = modelName.includes('DALL-E 3') ? 'DALL-E 3' : 'DALL-E 2';
  }
  // o3 models
  else if (modelName?.includes('o3-mini')) {
    icon = <ImageIcon style={{ fontSize: '16px' }} />;
    color = '#10a37f'; // OpenAI green
    bgColor = '#e6f6f2';
    displayName = 'O3 MINI';
  }
  // Imagen models
  else if (modelName?.includes('Imagen') || modelName?.includes('gemini-2.0-flash-exp-image-generation')) {
    icon = <ImageIcon style={{ fontSize: '16px' }} />;
    color = '#4285F4'; // Google blue for Imagen
    bgColor = '#e8f0fe';
    displayName = 'IMAGEN 3';
  }
  // OpenAI models
  else if (modelName?.includes('GPT') || modelName?.includes('OpenAI')) {
    icon = <AutoAwesomeIcon style={{ fontSize: '16px' }} />;
    color = '#10a37f'; // OpenAI green
    bgColor = '#e6f6f2';

    // Extract model name for cleaner display
    if (modelName.includes('GPT-4')) {
      displayName = 'GPT-4';
    } else if (modelName.includes('GPT-3.5')) {
      displayName = 'GPT-3.5';
    } else if (modelName.includes('4o mini')) {
      displayName = 'GPT-4o mini';
    } else if (modelName.includes('4o')) {
      displayName = 'GPT-4o';
    } else if (modelName.includes('o1-mini')) {
      displayName = 'o1 mini';
    }
  }
  // Gemini models
  else if (modelName?.includes('Gemini')) {
    icon = <PsychologyIcon style={{ fontSize: '16px' }} />;
    color = '#8e44ad'; // Gemini purple
    bgColor = '#f5f0f7';

    // Extract model name for cleaner display
    displayName = modelName.includes('Gemini 2.0') ? 'GEMINI 2.0' :
      modelName.includes('1.5') ? 'GEMINI 1.5' : modelName;
  }
  // Claude models
  else if (modelName?.includes('Claude')) {
    icon = <PsychologyIcon style={{ fontSize: '16px' }} />;
    color = '#5436DA'; // Anthropic purple
    bgColor = '#f7f5ff';

    // Extract model name for cleaner display
    if (modelName.includes('3.5 Sonnet')) {
      displayName = 'CLAUDE 3.5';
    } else if (modelName.includes('3.7 Sonnet')) {
      displayName = 'CLAUDE 3.7';
    } else if (modelName.includes('3 Opus')) {
      displayName = 'CLAUDE OPUS';
    } else if (modelName.includes('3 Sonnet')) {
      displayName = 'CLAUDE SONNET';
    } else if (modelName.includes('3 Haiku')) {
      displayName = 'CLAUDE HAIKU';
    } else {
      displayName = 'CLAUDE';
    }
  }
  else if (modelName.includes('Llama') || modelName.includes('Meta')) {
    icon = <SmartToyIcon style={{ fontSize: '16px' }} />;
    color = '#1877f2'; // Meta blue
    bgColor = '#f0f4fa';

    // Simplified display name for Llama models
    if (modelName.includes('3.2-3B')) {
      displayName = 'LLAMA 3.2';
    } else if (modelName.includes('3.1')) {
      displayName = 'LLAMA 3.1';
    } else {
      // Extract version if found
      const versionMatch = modelName.match(/Llama-(\d+\.\d+)/i);
      displayName = versionMatch ? `LLAMA ${versionMatch[1]}` : 'LLAMA';
    }
  } else if (modelName.includes('DeepSeek')) {
    icon = <SchoolIcon style={{ fontSize: '16px' }} />;
    color = '#e67e22'; // DeepSeek orange
    bgColor = '#fdf4ec';

    // Extract model name for cleaner display
    if (modelName.includes('R1')) {
      displayName = 'DEEPSEEK R1';
    } else {
      displayName = modelName.includes('67b') ? 'DEEPSEEK-LLM' : 'DEEPSEEK';
    }
  }

  return { icon, color, bgColor, displayName };
};

