const InlineCode = styled('code')(({ theme }) => ({
  backgroundColor: 'rgba(45, 45, 45, 0.5)', // More subtle background
  color: '#ffffff',
  padding: theme.spacing(0.3, 0.6),
  borderRadius: '3px',
  fontFamily: '"Roboto Mono", monospace',
  fontSize: '0.9em',
  border: '1px solid rgba(255, 255, 255, 0.1)',
  marginLeft: '2px',
  marginRight: '2px',
  wordBreak: 'break-word',
  boxShadow: 'none', // Remove box shadow
  display: 'inline-flex',
  alignItems: 'center',
  position: 'relative',
  top: '-1px',
}));

const ChatContent = forwardRef((props, ref) => {
  // const [loading, setLoading] = useState(false);
  const { t } = useTranslation();
  const { user } = useAuth();
  const { dragToggle, handleFileChange, handleDrop } = useSidebar();
  const dispatch = useDispatch();
  const messages = useSelector((state) => state.chat.messages);
  const [displayMessages, setDisplayMessages] = useState([]);
  const messagesEndRef = useRef(null); // Ref to the chat container
  const fileUploadRef = useRef(null);
  const selectedChatLog = useSelector((state) => state.chat.selectedChatLog);
  const activeChatId = useSelector((state) => state.chat.activeChatId);
  const selectedModel = useSelector((state) => state.setting.model);
  const [userScrolled, setUserScrolled] = useState(false);
  const [showScrollButton, setShowScrollButton] = useState(false);
  const [lastScrollTop, setLastScrollTop] = useState(0);
  const scrollTimerRef = useRef(null);
  const [modalImage, setModalImage] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const isGenerating = useSelector((state) => state.chat.isGenerating);
  const [userAtBottom, setUserAtBottom] = useState(true);
  const [file, setFile] = useState(null);
  const [copiedCode, setCopiedCode] = useState(null);
  const chatContainerRef = useRef(null);
  // Expose methods to parent via ref

  useEffect(() => {
    const chatContainer = chatContainerRef.current;
    if (chatContainer) {
      const handleScroll = (e) => {
        if (e && e.target) {
          const { scrollTop, scrollHeight, clientHeight } = e.target;
          const isScrolledToBottom = Math.abs(scrollHeight - scrollTop - clientHeight) < 10;
          setUserAtBottom(isUserAtBottom());
          // Update userScrolled based on whether we're at the bottom
          if (!isScrolledToBottom) {
            // User has scrolled up - set userScrolled to true regardless of generation state
            setUserScrolled(true);

            // When scrolling down (but not at bottom yet), show the button
            if (scrollTop > lastScrollTop && scrollTop < scrollHeight - clientHeight - 20) {
              setShowScrollButton(true);

              // Clear any existing timer
              if (scrollTimerRef.current) {
                clearTimeout(scrollTimerRef.current);
              }

              // Set a timer to hide the button if user stops scrolling
              scrollTimerRef.current = setTimeout(() => {
                setShowScrollButton(false);
              }, 2000);
            }
          } else {
            // At the bottom - reset states
            setUserScrolled(false);
            setShowScrollButton(false);

            if (scrollTimerRef.current) {
              clearTimeout(scrollTimerRef.current);
            }
          }

          // Save current scroll position
          setLastScrollTop(scrollTop);
        }
      };

      chatContainer.addEventListener('scroll', handleScroll);

      // Clean up the event listener when the component unmounts
      return () => {
        chatContainer.removeEventListener('scroll', handleScroll);
      };
    }
  }, []);

  useImperativeHandle(ref, () => ({
    handleScroll: (e) => {
      console.log("using scroll");
      if (e && e.target) {
        const { scrollTop, scrollHeight, clientHeight } = e.target;
        const isScrolledToBottom = Math.abs(scrollHeight - scrollTop - clientHeight) < 10;

        // Update userScrolled based on whether we're at the bottom
        if (!isScrolledToBottom) {
          // User has scrolled up - set userScrolled to true regardless of generation state
          setUserScrolled(true);

          // When scrolling down (but not at bottom yet), show the button
          if (scrollTop > lastScrollTop && scrollTop < scrollHeight - clientHeight - 20) {
            setShowScrollButton(true);

            // Clear any existing timer
            if (scrollTimerRef.current) {
              clearTimeout(scrollTimerRef.current);
            }

            // Set a timer to hide the button if user stops scrolling
            scrollTimerRef.current = setTimeout(() => {
              setShowScrollButton(false);
            }, 2000);
          }
        } else {
          // At the bottom - reset states
          setUserScrolled(false);
          setShowScrollButton(false);

          if (scrollTimerRef.current) {
            clearTimeout(scrollTimerRef.current);
          }
        }

        // Save current scroll position
        setLastScrollTop(scrollTop);
      }
    }
  }));

  // Clean up timer when component unmounts
  useEffect(() => {
    return () => {
      if (scrollTimerRef.current) {
        clearTimeout(scrollTimerRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (user) {
      dispatch(getUserChat(user.id))
    }
  }, [user]);

  useEffect(() => {
    // Simulate adding new content and auto-scrolling
    // const interval = setInterval(() => {
    //   if (!userScrolled) {
    //     scrollToBottom();
    //   }
    // }, 3000); // Simulate adding new content every 3 seconds

    // return () => clearInterval(interval);
  }, [userScrolled]);

  const isUserAtBottom = () => {
    const container = chatContainerRef.current;
    if (!container) return false;
    const threshold = 50; // px from bottom
    return container.scrollHeight - container.scrollTop - container.clientHeight < threshold;
  };

  useEffect(() => {
    if (userAtBottom) {
      scrollToBottom();
    }
  }, [displayMessages, selectedChatLog])

  useEffect(() => {
    fileUploadRef.current?.scrollIntoView({ behavior: 'smooth' })
  }, [dragToggle])

  useEffect(() => {
    if (selectedChatLog) {
      setDisplayMessages(selectedChatLog);

      // Auto-scroll to bottom only when a new user message is added
      // Removed the auto-scroll during generation
      const isNewUserMessage = selectedChatLog.length > 0 &&
        displayMessages.length < selectedChatLog.length &&
        selectedChatLog[selectedChatLog.length - 1]?.role === 'user';

      if (isNewUserMessage) {
        // Use a small timeout to ensure the DOM has updated with the new content
        setTimeout(() => {
          scrollToBottom();
        }, 50);
      }
    }
  }, [activeChatId, selectedChatLog, isGenerating]);

  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      // Try multiple approaches to ensure scrolling works
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });

      // Also try direct DOM manipulation as a fallback
      const chatContainer = document.querySelector('.MuiBox-root');
      if (chatContainer) {
        chatContainer.scrollTop = chatContainer.scrollHeight;
      }
    }
    setUserScrolled(false);
    setShowScrollButton(false);
  };

  // Copy code function
  const copyToClipboard = (code) => {
    navigator.clipboard.writeText(code).then(() => {
      setCopiedCode(code);
      // Reset copied status after 2 seconds
      setTimeout(() => {
        setCopiedCode(null);
      }, 2000);
    });
  };

  // Custom renderers for ReactMarkdown
  const renderers = {
    code({ node, inline, className, children, ...props }) {
      const match = /language-(\w+)/.exec(className || '');
      const code = String(children).replace(/\n$/, '');

      if (inline) {
        return <InlineCode {...props}>{children}</InlineCode>;
      }

      const language = match ? match[1] : '';
      const displayLanguage = language ? language.toUpperCase() : '';

      // Highlight potential filenames in code blocks
      // This will look for pattern like: filename.py, file.js, etc.
      const highlightedCode = code.replace(
        /([a-zA-Z0-9_-]+\.(py|js|ts|html|css|jsx|tsx|json|md|yml|yaml|xml|csv|txt))/g,
        '<span class="filename">$1</span>'
      );

      return (
        <CodeBlock>
          {displayLanguage && (
            <Box className="code-language">{displayLanguage}</Box>
          )}
          <Button
            className="copy-button"
            onClick={() => copyToClipboard(code)}
            aria-label="Copy code"
            size="small"
          >
            {copiedCode === code ? (
              <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', width: '100%', height: '100%' }}>
                <Typography variant="caption" sx={{ fontSize: '0.6rem' }}>Copied</Typography>
              </Box>
            ) : (
              <ContentCopyIcon />
            )}
          </Button>
          <pre style={{ width: "fit-content" }}>
            <code className={className} {...props}>
              {children}
            </code>
          </pre>
        </CodeBlock>
      );
    },
    // Add special styling for any filenames like calculator.py
    p(props) {
      const children = React.Children.toArray(props.children);

      // Process the children to find text that looks like filenames
      const processedChildren = children.map(child => {
        if (typeof child === 'string') {
          // Look for filename patterns like calculator.py or python calculator.py
          const parts = child.split(/\b((?:python\s+)?[a-zA-Z0-9_-]+\.[a-zA-Z0-9]+)\b/g);

          if (parts.length > 1) {
            return parts.map((part, i) => {
              // Check if this part matches a filename pattern
              if (/^(?:python\s+)?[a-zA-Z0-9_-]+\.[a-zA-Z0-9]+$/.test(part)) {
                // It's a filename, style it with no background color
                return (
                  <Box
                    key={i}
                    component="span"
                    sx={{
                      display: 'inline-block',
                      bgcolor: 'transparent',
                      color: '#e6c07b',
                      padding: '2px 4px',
                      borderRadius: '2px',
                      fontFamily: '"Roboto Mono", monospace',
                      fontSize: '0.9em',
                      fontWeight: 'bold',
                      mx: 0.5,
                      border: '1px solid rgba(230, 192, 123, 0.3)',
                    }}
                  >
                    {part}
                  </Box>
                );
              }

              // Regular text
              return part;
            });
          }
        }

        // Not a string or no filename found, return unchanged
        return child;
      });

      return <Typography {...props}>{processedChildren}</Typography>;
    }
  };

  // Handle opening image in modal
  const handleImageClick = (imageUrl, revisedPrompt) => {
    setModalImage({ url: imageUrl, revisedPrompt });
    setModalOpen(true);
  };

  // Handle closing the modal
  const handleCloseModal = () => {
    setModalOpen(false);
  };

  // Handle downloading the image
  const handleDownloadImage = () => {
    if (!modalImage?.url) return;

    // For data URLs (like those from Imagen which start with data:image/), 
    // we can download directly without going through the backend
    if (modalImage.url.startsWith('data:image/')) {
      const link = document.createElement('a');
      link.href = modalImage.url;
      link.download = 'generated-image.png';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      return;
    }

    // For URLs that need to go through the backend (like DALL-E)
    // Create the download URL that goes through our backend
    const downloadUrl = `${process.env.REACT_APP_BACKEND_URL}/api/download_image/?url=${encodeURIComponent(modalImage.url)}`;

    try {
      // Get CSRF token from cookies
      const csrfToken = Cookies.get('csrftoken');

      // Create a link with CSRF token in header
      const xhr = new XMLHttpRequest();
      xhr.open('GET', downloadUrl, true);

      // Add CSRF token if available
      if (csrfToken) {
        xhr.setRequestHeader('X-CSRFToken', csrfToken);
      }

      xhr.responseType = 'blob';

      xhr.onload = function () {
        if (this.status === 200) {
          const blob = new Blob([this.response], { type: 'image/png' });
          const url = window.URL.createObjectURL(blob);
          const a = document.createElement('a');
          a.style.display = 'none';
          a.href = url;
          a.download = 'generated-image.png';
          document.body.appendChild(a);
          a.click();
          window.URL.revokeObjectURL(url);
          document.body.removeChild(a);
        } else {
          // Fallback to the simple method if XHR fails
          window.open(downloadUrl, '_blank');
        }
      };

      xhr.onerror = function () {
        // Fallback to the simple method if XHR fails
        window.open(downloadUrl, '_blank');
      };

      xhr.send();
    } catch (error) {
      console.error("Error downloading image:", error);
      // Fallback to the simple window.open method if any errors occur
      window.open(downloadUrl, '_blank');
    }
  };

  // Add styled component for the modal
  const StyledImageModal = styled(Dialog)(({ theme }) => ({
    '& .MuiDialog-paper': {
      backgroundColor: 'rgba(255, 255, 255, 0.95)',
      backgroundImage: 'linear-gradient(to bottom, rgba(255, 255, 255, 0.97), rgba(245, 245, 245, 0.94))',
      borderRadius: theme.spacing(2),
      overflow: 'hidden',
      boxShadow: '0 10px 40px rgba(0, 0, 0, 0.15)',
      border: '1px solid rgba(0, 0, 0, 0.08)',
      maxWidth: '90vw',
      maxHeight: '90vh',
    },
    '& .MuiDialogContent-root': {
      padding: theme.spacing(2),
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
    }
  }));

  const ImageModalActions = styled(Box)(({ theme }) => ({
    display: 'flex',
    justifyContent: 'center',
    gap: theme.spacing(2),
    marginTop: theme.spacing(2),
    width: '100%',
    '& .MuiButton-root': {
      borderRadius: theme.spacing(1),
      padding: theme.spacing(0.75, 2),
      minWidth: '120px',
      transition: 'all 0.2s ease',
      fontWeight: 500,
    },
    '& .download-btn': {
      backgroundColor: '#265A9E',
      '&:hover': {
        backgroundColor: '#1e4c8a',
        transform: 'translateY(-2px)',
        boxShadow: '0 5px 10px rgba(30, 76, 138, 0.2)',
      },
    },
    '& .close-btn': {
      borderColor: '#ccc',
      color: '#666',
      '&:hover': {
        borderColor: '#999',
        backgroundColor: 'rgba(0, 0, 0, 0.03)',
      },
    }
  }));

  const ImagePromptText = styled(Typography)(({ theme }) => ({
    color: '#555',
    fontSize: '0.9rem',
    textAlign: 'center',
    maxWidth: '90%',
    marginTop: theme.spacing(1.5),
    fontStyle: 'italic',
    lineHeight: 1.5,
    padding: theme.spacing(1, 2),
    backgroundColor: 'rgba(0, 0, 0, 0.03)',
    borderRadius: theme.spacing(1),
    border: '1px solid rgba(0, 0, 0, 0.06)',
  }));

  // Add a styled component for user questions about images
  const ImageQuestionBadge = styled(Box)(({ theme }) => ({
    backgroundColor: 'rgba(255, 255, 255, 0.15)',
    borderRadius: theme.spacing(1),
    padding: theme.spacing(1, 1.5),
    marginBottom: theme.spacing(1.5),
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(1),
    backdropFilter: 'blur(5px)',
    border: '1px solid rgba(255, 255, 255, 0.2)',
    color: 'white',
    fontSize: '0.95rem',
    fontWeight: 500,
    boxShadow: '0 2px 8px rgba(0, 0, 0, 0.08)',
    position: 'relative',
    '&:before': {
      content: '""',
      position: 'absolute',
      bottom: -8,
      left: 12,
      width: 16,
      height: 16,
      backgroundColor: 'rgba(255, 255, 255, 0.15)',
      transform: 'rotate(45deg)',
      borderRight: '1px solid rgba(255, 255, 255, 0.2)',
      borderBottom: '1px solid rgba(255, 255, 255, 0.2)',
      zIndex: 0,
    }
  }));

  // Prevent auto-scroll during message generation
  useEffect(() => {
    if (isGenerating && showScrollButton) {
      setShowScrollButton(true);
    }
  }, [isGenerating]);

  // Fetch CSRF token on component mount if it's not already set
  useEffect(() => {
    const fetchCSRFToken = async () => {
      try {
        // Check if CSRF token already exists
        if (!Cookies.get('csrftoken')) {
          console.log('No CSRF token found for chat, fetching a new one...');
          const backendUrl = process.env.REACT_APP_BACKEND_URL || 'http://localhost:8000';

          const response = await fetch(`${backendUrl}/api/get_csrf_token/`, {
            credentials: 'include'
          });

          if (response.status === 200) {
            const token = Cookies.get('csrftoken');
            console.log('CSRF token fetched successfully:', token ? 'Token received' : 'No token set');
          } else {
            console.warn('Failed to fetch CSRF token:', response.status);
          }
        } else {
          console.log('CSRF token already exists for chat');
        }
      } catch (error) {
        console.error('Error fetching CSRF token:', error);
        // Continue without CSRF token - backend will handle exempt views
      }
    };

    fetchCSRFToken();
  }, []);

  return (
    <Box
      sx={{
        height: '100%',
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        overflowY: 'auto',
      }}
      ref={chatContainerRef}
    >
      <Box sx={{ p: 2 }}>
        <span ref={fileUploadRef} />

        {/* Main chat content */}
        <Box
          sx={{
            pt: '5px',
            mb: {
              xs: 3,
              md: 5
            }
          }}
        >
          {!dragToggle ? (
            <Box sx={{
              mx: {
                xs: '20px',
                md: "90px"
              },
            }}>
              <Typography variant="h4" lineHeight={2} sx={{ fontWeight: 'bold', color: '#265A9E' }}>{t("someIdeas")}</Typography>
              {selectedModel?.id === 'dall-e-3' ? (
                <Box sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  mt: 2,
                  minHeight: '400px'
                }}>
                  <Box sx={{
                    bgcolor: "white",
                    color: '#667085',
                    boxShadow: "0px 0px 2px #6A7199",
                    padding: "12px 16px",
                    borderRadius: "10px",
                    marginBottom: "8px",
                    maxWidth: "fit-content"
                  }}>
                    <Typography variant="body1" sx={{ fontSize: '14px' }}>
                      Cute baby panda sits surrounded by glowing fireflies  3D style
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      height: '20px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      marginY: '8px',
                      position: 'relative',
                      zIndex: 0
                    }}
                  >
                    <svg width="12" height="20" viewBox="0 0 12 20" fill="none">
                      <path
                        d="M6 0V17M6 17L1 12M6 17L11 12"
                        stroke="#667085"
                        strokeWidth="1.5"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                      />
                    </svg>
                  </Box>
                  <ChatImageWrapper>
                    <CardActionArea
                      onClick={() => handleImageClick("/baby-panda.jpeg")}
                      sx={{
                        borderRadius: 2,
                        overflow: 'hidden',
                      }}
                    >
                      <Box
                        component="img"
                        src="/baby-panda.jpeg"
                        alt="Baby Panda Example"
                        sx={{
                          width: '200px',
                          height: 'auto',
                          display: 'block',
                        }}
                      />
                    </CardActionArea>
                  </ChatImageWrapper>
                </Box>
              ) : selectedModel?.id === 'gemini-2.0-flash-exp-image-generation' ? (
                <Box sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  mt: 2,
                  minHeight: '400px'
                }}>
                  <Box sx={{
                    bgcolor: "white",
                    color: '#667085',
                    boxShadow: "0px 0px 2px #6A7199",
                    padding: "12px 16px",
                    borderRadius: "10px",
                    marginBottom: "8px",
                    maxWidth: "fit-content"
                  }}>
                    <Typography variant="body1" sx={{ fontSize: '14px' }}>
                      Pixar style, person, happy in a hospital using technology
                    </Typography>
                  </Box>
                  <Box
                    sx={{
                      height: '20px',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      marginY: '8px',
                      position: 'relative',
                      zIndex: 0
                    }}
                  >
                    <svg width="12" height="20" viewBox="0 0 12 20" fill="none">
                      <path
                        d="M6 0V17M6 17L1 12M6 17L11 12"
                        stroke="#667085"
                        strokeWidth="1.5"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                      />
                    </svg>
                  </Box>
                  <ChatImageWrapper>
                    <CardActionArea
                      onClick={() => handleImageClick("/boy-in-hospital.jpeg")}
                      sx={{
                        borderRadius: 2,
                        overflow: 'hidden',
                      }}
                    >
                      <Box
                        component="img"
                        src="/boy-in-hospital.jpeg"
                        alt="Boy in Hospital Example"
                        sx={{
                          width: '200px',
                          height: 'auto',
                          display: 'block',
                        }}
                      />
                    </CardActionArea>
                  </ChatImageWrapper>
                </Box>
              ) : (
                <Box sx={{
                  bgcolor: "white",
                  color: '#667085',
                  boxShadow: "0px 0px 2px #6A7199",
                  padding: "18px",
                  borderRadius: "10px"
                }}>
                  <Typography lineHeight={2}>{t("writePeom")} &hearts;</Typography>
                  <Typography lineHeight={2}>{t("writeAskingPromotion")}</Typography>
                  <Typography lineHeight={2}>{t("writeSong")}&#9835;</Typography>
                  <Typography lineHeight={2}>{t("writeCode")}</Typography>
                </Box>
              )}
            </Box>
          ) : (
            <Box sx={{
              mx: {
                xs: '20px',
                md: "90px"
              },
              textAlign: 'center'
            }}>
              <Typography variant="h4" lineHeight={2} sx={{ fontWeight: 'bold', color: '#265A9E' }}>{t("Drop your documents here and search through them.")}</Typography>
              <Typography lineHeight={2} sx={{ mb: 1, color: '#667085' }}>{t("Eg: Find all instances in the book where the character Tom was angry.")}</Typography>
              <Box
                sx={{
                  background: '#D8E1FF',
                  padding: 2,
                  borderRadius: 3,
                  cursor: 'pointer'
                }}
                onClick={() => document.getElementById('file-upload').click()}
                onDragOver={(e) => e.preventDefault()}
                onDrop={handleDrop}
              >
                <Box
                  sx={{
                    border: '2px dashed #667085',
                    borderRadius: 2,
                    padding: '20px',
                    textAlign: 'center',
                    margin: 'auto',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center',
                    backgroundColor: '#D8E1FF'
                  }}
                >
                  <>
                    <input
                      type="file"
                      id="file-upload"
                      style={{ display: 'none' }}
                      onChange={handleFileChange}
                      accept=".pdf,.doc,.docx,.png"
                    />
                    <svg width="69" height="60" viewBox="0 0 69 60" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M36.028 14.7458L36.1203 14.7733L36.1243 14.7688C36.5619 14.8481 36.9961 14.586 37.1247 14.1519C38.2963 10.2152 41.9874 7.46504 46.0998 7.46504C46.5867 7.46504 46.9816 7.07016 46.9816 6.5833C46.9816 6.09643 46.5867 5.70156 46.0998 5.70156C41.0457 5.70156 36.7985 9.06665 35.4348 13.6493C35.2956 14.1162 35.5615 14.6067 36.028 14.7458Z" fill="#003DF5" stroke="#F9FFF9" strokeWidth="0.3" />
                      <path d="M56.3438 42.4384H51.9534C51.5494 42.4384 51.2217 42.1107 51.2217 41.7067C51.2217 41.3027 51.5494 40.9749 51.9534 40.9749H56.3438C62.3956 40.9749 67.3197 36.0509 67.3197 29.999C67.3197 23.9471 62.3956 19.023 56.3438 19.023H56.2382C56.026 19.023 55.8242 18.9311 55.6852 18.7706C55.5462 18.6101 55.4834 18.3974 55.5138 18.1873C55.5791 17.7315 55.612 17.2737 55.612 16.8279C55.612 11.5829 51.3444 7.31531 46.0995 7.31531C44.059 7.31531 42.1131 7.95296 40.4719 9.15978C40.1112 9.42478 39.599 9.30718 39.3905 8.91047C34.7425 0.0596993 22.6023 -1.12887 16.3082 6.57053C13.6568 9.81417 12.615 14.0336 13.4498 18.146C13.5418 18.6002 13.1942 19.0236 12.7327 19.0236H12.4395C6.3876 19.0236 1.46353 23.9477 1.46353 29.9996C1.46353 36.0514 6.3876 40.9755 12.4395 40.9755H16.8298C17.2338 40.9755 17.5615 41.3032 17.5615 41.7072C17.5615 42.1113 17.2338 42.439 16.8298 42.439H12.4395C5.5805 42.439 0 36.8585 0 29.9995C0 23.3329 5.27155 17.8742 11.8651 17.5731C11.2457 13.3066 12.4301 9.00295 15.1751 5.64437C21.9138 -2.5996 34.828 -1.67556 40.2871 7.51707C42.0287 6.42522 44.0215 5.85244 46.0992 5.85244C52.4538 5.85244 57.4892 11.261 57.0486 17.58C63.5813 17.9463 68.7829 23.3763 68.7829 29.999C68.7829 36.8585 63.2024 42.4384 56.3434 42.4384L56.3438 42.4384Z" fill="#003DF5" />
                      <path d="M15.85 41.2935C15.85 51.4634 24.1237 59.737 34.2935 59.737C44.4634 59.737 52.737 51.4633 52.737 41.2935C52.737 31.1235 44.4634 22.85 34.2935 22.85C24.1235 22.85 15.85 31.1237 15.85 41.2935ZM17.6138 41.2935C17.6138 32.0966 25.0964 24.6138 34.2935 24.6138C43.4904 24.6138 50.9732 32.0964 50.9732 41.2935C50.9732 50.4904 43.4904 57.9732 34.2935 57.9732C25.0966 57.9732 17.6138 50.4905 17.6138 41.2935Z" fill="#003DF5" stroke="#F9FFF9" strokeWidth="0.3" />
                      <path d="M33.9425 48.6577C33.9425 49.0363 34.2496 49.3434 34.6282 49.3434C35.0068 49.3434 35.314 49.0367 35.314 48.6577V34.7291C35.314 34.3504 35.0069 34.0434 34.6282 34.0434C34.2496 34.0434 33.9425 34.3504 33.9425 34.7291V48.6577Z" fill="#003DF5" stroke="#483EA8" strokeWidth="0.3" />
                      <path d="M34.6281 35.6999L30.8274 39.5006L34.6281 35.6999ZM34.6281 35.6999L38.4289 39.5007C38.5626 39.6344 38.7386 39.7015 38.9137 39.7015L34.6281 35.6999ZM29.8576 39.5007C30.1254 39.7685 30.5597 39.7686 30.8273 39.5007L38.9138 39.7015C39.0886 39.7015 39.2647 39.635 39.3987 39.5006C39.6665 39.2327 39.6665 38.7988 39.3986 38.5309L35.113 34.2452C34.8452 33.9775 34.4108 33.9773 34.1432 34.2452C34.1432 34.2453 34.1431 34.2453 34.1431 34.2453L29.8576 38.5309C29.5897 38.7988 29.5897 39.2328 29.8576 39.5007Z" fill="#003DF5" stroke="#483EA8" strokeWidth="0.3" />
                    </svg>

                    <Typography variant="body1" mt={3} sx={{ color: '#667085' }} fontSize={20} >
                      {t("Drag & drop files or Browse")}
                    </Typography>
                    <Typography variant="body1" sx={{ color: '#66708580' }}>{t("Supported formats: PDF, Word, and PNG")}</Typography>
                  </>
                </Box>
              </Box>
            </Box>
          )}
        </Box>

        {/* Add visual separator when there are no messages */}
        {displayMessages.length === 0 && !dragToggle && (
          <Box
            sx={{
              width: '100%',
              display: 'flex',
              justifyContent: 'center',
              mb: 5
            }}
          >
            <Box
              sx={{
                width: { xs: '60%', md: '40%' },
                height: '1px',
                backgroundColor: 'rgba(102, 112, 133, 0.15)',
                borderRadius: '1px'
              }}
            />
          </Box>
        )}

        {/* Chat messages */}
        {displayMessages.map((message, index) => (
          <MessageContainer
            key={index}
            role={message.role}
          >
            <MessageContent role={message.role}>
              {message.role === 'assistant' && message.model && (
                <Box
                  sx={{
                    display: 'flex',
                    alignItems: 'center',
                    mb: 1,
                    gap: 1,
                    px: 1,
                    py: 0.5,
                    backgroundColor: 'rgba(248, 249, 252, 0.9)',
                    borderRadius: 1,
                    border: '1px solid rgba(200, 200, 200, 0.2)',
                    alignSelf: 'flex-start',
                    boxShadow: '0 2px 4px rgba(0, 0, 0, 0.03)'
                  }}
                >
                  {(() => {
                    const { icon, color, bgColor, displayName } = getModelDetails(message.model);
                    return (
                      <>
                        <Avatar
                          sx={{
                            width: 20,
                            height: 20,
                            bgcolor: bgColor,
                            border: `1px solid ${color}50`
                          }}
                        >
                          {React.cloneElement(icon, { style: { ...icon.props.style, color, fontSize: '12px' } })}
                        </Avatar>
                        <Typography
                          sx={{
                            color: '#666',
                            fontWeight: 600,
                            fontSize: '0.75rem',
                            textTransform: 'uppercase',
                            letterSpacing: '0.5px'
                          }}
                        >
                          {displayName}
                        </Typography>
                      </>
                    );
                  })()}
                </Box>
              )}

              {message.role === 'assistant' ? (
                <CardWrapperSecondary>
                  {message.type === 'image_generation' ? (
                    <ChatAssistantImageMessage>
                      <Typography variant="body1" className="message-text">
                        {message.content}
                      </Typography>
                      {message.images ? (
                        <Box className="image-grid">
                          {message.images.map((image, imgIndex) => (
                            <ChatImageWrapper key={imgIndex}>
                              <CardActionArea
                                onClick={() => handleImageClick(image.url)}
                                sx={{
                                  borderRadius: 2,
                                  overflow: 'hidden',
                                }}
                              >
                                <Box
                                  component="img"
                                  src={image.url}
                                  alt={`Generated image ${imgIndex + 1}`}
                                  sx={{
                                    width: '100%',
                                    height: 'auto',
                                  }}
                                />
                              </CardActionArea>
                            </ChatImageWrapper>
                          ))}
                        </Box>
                      ) : (
                        <Typography color="error">Image data not available</Typography>
                      )}
                    </ChatAssistantImageMessage>
                  ) : (
                    <ReactMarkdown rehypePlugins={[rehypeHighlight]} components={renderers}>{message.content}</ReactMarkdown>
                  )}
                </CardWrapperSecondary>
              ) : (
                <CardWrapperPrimary>
                  {message.file && (
                    <Box sx={{
                      position: 'relative',
                      p: '7px',
                      border: '1px solid rgba(255, 255, 255, 0.3)',
                      minWidth: 250,
                      borderRadius: 1,
                      display: 'flex',
                      alignItems: 'center',
                      gap: 1,
                      color: 'white',
                      mx: 1,
                      mb: 1,
                      backgroundColor: 'rgba(255, 255, 255, 0.1)',
                    }}>
                      <Box sx={{ width: 30, height: 30 }}>
                        <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M17.8033 10.4665L9.94233 18.0854C8.75873 19.2326 8.75873 21.0935 9.94233 22.2407V22.2407C11.1259 23.3878 13.046 23.3878 14.2296 22.2407L24.5925 12.1969C26.7634 10.0929 26.7634 6.68204 24.5925 4.57802V4.57802C22.4216 2.47399 18.9024 2.47399 16.7315 4.57802L6.36862 14.6218C3.21046 17.6827 3.21046 22.6434 6.36862 25.7043V25.7043C9.52678 28.7652 14.6452 28.7652 17.8033 25.7043L24.0924 19.6089" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        </svg>
                      </Box>
                      <span style={{ overflow: 'hidden', textOverflow: "ellipsis", whiteSpace: "nowrap" }}>{message.file}</span>
                    </Box>
                  )}
                  <ChatUserMessage>
                    {message.base64Encoded && (
                      <>
                        {/* Add question badge for image questions */}
                        {message.content &&
                          <ImageQuestionBadge>
                            <Box
                              sx={{
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                width: 22,
                                height: 22,
                                borderRadius: '50%',
                                backgroundColor: 'rgba(255, 255, 255, 0.2)',
                                flexShrink: 0
                              }}
                            >
                              <svg width="12" height="12" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M12 22C17.5228 22 22 17.5228 22 12C22 6.47715 17.5228 2 12 2C6.47715 2 2 6.47715 2 12C2 17.5228 6.47715 22 12 22Z" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                <path d="M12 17H12.01" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                <path d="M9.09 9.00002C9.3251 8.33169 9.78915 7.76813 10.4 7.40915C11.0108 7.05018 11.7289 6.91896 12.4272 7.03873C13.1255 7.15851 13.7588 7.52154 14.2151 8.06353C14.6713 8.60553 14.9211 9.29152 14.92 10C14.92 12 11.92 13 11.92 13" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                              </svg>
                            </Box>
                            <Typography component="span" sx={{ fontSize: '0.9rem', wordBreak: 'break-word' }}>
                              {typeof message.content === 'string' ? message.content :
                                Array.isArray(message.content) && message.content[0]?.text ?
                                  message.content[0].text : 'About this image'}
                            </Typography>
                          </ImageQuestionBadge>
                        }
                        <ChatImageWrapper sx={{ mb: 2, border: '1px solid rgba(255, 255, 255, 0.2)', boxShadow: '0 4px 12px rgba(0, 0, 0, 0.12)', maxWidth: 300 }}>
                          <CardActionArea
                            onClick={() => handleImageClick(message.base64Encoded)}
                            sx={{
                              borderRadius: 2,
                              overflow: 'hidden',
                            }}
                          >
                            <Box
                              component="img"
                              sx={{
                                width: '100%',
                                height: 'auto',
                                display: 'block',
                              }}
                              src={message.base64Encoded}
                              alt="User uploaded image"
                            />
                          </CardActionArea>
                        </ChatImageWrapper>
                      </>
                    )}
                    <Box className="message-text">
                      {Array.isArray(message.content) ? (
                        message.content[1]?.image_url?.url ? (
                          <>
                            <ImageQuestionBadge>
                              <Box
                                sx={{
                                  display: 'flex',
                                  alignItems: 'center',
                                  justifyContent: 'center',
                                  width: 22,
                                  height: 22,
                                  borderRadius: '50%',
                                  backgroundColor: 'rgba(255, 255, 255, 0.2)',
                                  flexShrink: 0
                                }}
                              >
                                <svg width="12" height="12" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                  <path d="M12 22C17.5228 22 22 17.5228 22 12C22 6.47715 17.5228 2 12 2C6.47715 2 2 6.47715 2 12C2 17.5228 6.47715 22 12 22Z" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                  <path d="M12 17H12.01" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                  <path d="M9.09 9.00002C9.3251 8.33169 9.78915 7.76813 10.4 7.40915C11.0108 7.05018 11.7289 6.91896 12.4272 7.03873C13.1255 7.15851 13.7588 7.52154 14.2151 8.06353C14.6713 8.60553 14.9211 9.29152 14.92 10C14.92 12 11.92 13 11.92 13" stroke="white" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                                </svg>
                              </Box>
                              <Typography component="span" sx={{ fontSize: '0.9rem', wordBreak: 'break-word' }}>
                                {message.content[0]?.text || 'About this image'}
                              </Typography>
                            </ImageQuestionBadge>
                            <ChatImageWrapper>
                              <CardActionArea
                                onClick={() => handleImageClick(message.content[1].image_url.url)}
                                sx={{
                                  borderRadius: 2,
                                  overflow: 'hidden',
                                }}
                              >
                                <Box
                                  component="img"
                                  sx={{
                                    width: '100%',
                                    height: 'auto',
                                  }}
                                  src={message.content[1].image_url.url}
                                  alt="User uploaded image"
                                />
                              </CardActionArea>
                            </ChatImageWrapper>
                          </>
                        ) : (
                          <ReactMarkdown rehypePlugins={[rehypeHighlight]} components={renderers}>
                            {typeof message.content === 'string' ? message.content : 'Image message'}
                          </ReactMarkdown>
                        )
                      ) : (
                        // Only show the message content when there's no image
                        !message.base64Encoded && (
                          <ReactMarkdown rehypePlugins={[rehypeHighlight]} components={renderers}>
                            {message.content}
                          </ReactMarkdown>
                        )
                      )}
                    </Box>
                  </ChatUserMessage>
                </CardWrapperPrimary>
              )}
            </MessageContent>
          </MessageContainer>
        ))}

        {/* Element to scroll to */}
        <div ref={messagesEndRef} />
      </Box>

      {/* Scroll to bottom button */}
      {showScrollButton && (
        <Tooltip title={t('scrollToBottom')} placement="top" arrow>
          <ScrollToBottomFab
            size="small"
            onClick={scrollToBottom}
            aria-label={t('scrollToBottom')}
          >
            <KeyboardArrowDownIcon sx={{ fontSize: '18px' }} />
          </ScrollToBottomFab>
        </Tooltip>
      )}

      {/* Image Modal */}
      <StyledImageModal
        open={modalOpen}
        onClose={handleCloseModal}
        maxWidth="lg"
        TransitionComponent={Zoom}
        PaperProps={{
          elevation: 8,
        }}
      >
        <DialogContent sx={{ p: 2, overflow: 'hidden' }}>
          <Box
            component="img"
            src={modalImage?.url}
            alt="Expanded image"
            sx={{
              maxWidth: '100%',
              maxHeight: '70vh',
              borderRadius: 2,
              objectFit: 'contain',
              boxShadow: '0 4px 20px rgba(0, 0, 0, 0.08)',
            }}
          />

          {modalImage?.revisedPrompt && (
            <ImagePromptText variant="body2">
              {modalImage.revisedPrompt}
            </ImagePromptText>
          )}

          <ImageModalActions>
            <Button
              onClick={handleDownloadImage}
              variant="contained"
              startIcon={<DownloadIcon />}
              className="download-btn"
              sx={{ color: 'white' }}
            >
              Download
            </Button>
            <Button
              onClick={handleCloseModal}
              variant="outlined"
              className="close-btn"
            >
              Close
            </Button>
          </ImageModalActions>
        </DialogContent>
      </StyledImageModal>
    </Box>
  );
})

export default ChatContent;
